@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&family=Playfair+Display:wght@400;700&display=swap');

.introContainer {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    font-family: 'Roboto', sans-serif;
    color: #333;
}

/* Tiêu đề chính */
.introHeader {
    text-align: center;
    margin-bottom: 40px;
}

.introTitle {
    font-family: 'Playfair Display', serif;
    font-size: 3rem;
    color: red;
    margin-bottom: 20px;
}

.introSubtitle {
    font-size: 1.2rem;
    color: #7f8c8d;
}

/* Phần nội dung chính */
.section {
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    animation: fadeIn 1s ease-in-out;
}

/* Thay đổi hướng section xen kẽ */
.section:nth-child(even) {
    flex-direction: row-reverse;
}

/* Ảnh trong section */
.sectionImage {
    flex: 1;
    max-width: 50%;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.sectionImage:hover {
    transform: scale(1.05);
}

/* Nội dung văn bản */
.sectionContent {
    flex: 1;
    padding: 0 40px;
}

.sectionTitle {
    font-family: 'Playfair Display', serif;
    font-size: 2rem;
    color: #2c3e50;
    margin-bottom: 20px;
}

.sectionText {
    font-size: 1rem;
    line-height: 1.6;
    color: #34495e;
}

/* Hiệu ứng fade in */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

/* ✅ Responsive cho Tablet (dưới 1024px) */
@media (max-width: 1024px) {
    .introTitle {
        font-size: 2.5rem;
    }

    .section {
        flex-direction: column;
        text-align: center;
    }

    .sectionImage {
        max-width: 80%;
        margin-bottom: 20px;
    }

    .sectionContent {
        padding: 0 20px;
    }
}

/* ✅ Responsive cho Mobile (dưới 768px) */
@media (max-width: 768px) {
    .introContainer {
        padding: 30px 15px;
    }

    .introTitle {
        font-size: 2rem;
    }

    .introSubtitle {
        font-size: 1rem;
    }

    .section {
        flex-direction: column;
        text-align: center;
    }

    .sectionImage {
        max-width: 100%;
        margin-bottom: 15px;
    }

    .sectionContent {
        padding: 0;
    }

    .sectionTitle {
        font-size: 1.8rem;
    }

    .sectionText {
        font-size: 0.95rem;
    }
}

/* ✅ Responsive cho điện thoại rất nhỏ (dưới 414px - iPhone SE, Galaxy Fold) */
@media (max-width: 414px) {
    .introTitle {
        font-size: 1.8rem;
    }

    .sectionTitle {
        font-size: 1.5rem;
    }

    .sectionText {
        font-size: 0.9rem;
    }
}

/* ✅ Responsive cho điện thoại nhỏ hơn 375px (iPhone 5, Galaxy S8) */
@media (max-width: 375px) {
    .introContainer {
        padding: 20px 10px;
    }

    .introTitle {
        font-size: 1.6rem;
    }

    .sectionTitle {
        font-size: 1.4rem;
    }

    .sectionText {
        font-size: 0.85rem;
    }
}
